import React, {Component, Fragment} from 'react';
import {CSSTransition, TransitionGroup} from 'react-transition-group';
import {Route} from 'react-router-dom';
import './Members.css';
import '../../../assets/Common.css'
import {Link} from 'react-router-dom';
import axios from 'axios';
import moment from 'moment';
import {CSVLink, CSVDownload} from 'react-csv';
import {format} from 'date-fns';

import {
    Table,
    Button,
    Dropdown,
    UncontrolledButtonDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem,
    Form,
    FormGroup,
    Input,
    Row,
    Col,
    Card,
    Pagination,
    PaginationItem,
    PaginationLink,
    Modal,
    ModalHeader,
    ModalBody
} from 'reactstrap';

import {withRouter} from 'react-router-dom';

class BasicMembers extends Component {
    constructor() {
        super();
        this.state = {
            userData: [],
            currentPage: 1,
            searchKeyword: '',
            itemsPerPage: 10,
            selectedItem: '아이디',
            userDataStore: []
        };
    }

    checkProps = () => {

        if (this.props.match.params.returned === 'yes') {

            this.setState({searchKeyword: this.props.match.params.searchKeyword });
            this.setState({selectedItem: this.props.match.params.selectedItem });

            if (this.props.match.params.searchKeyword === 'null') {
                this.setState({searchKeyword: ''});
            }

            this.handleSearch();
        }
    }

    handleDropdownItemSelect = (item) => {

        this.setState({selectedItem: item});
    };

    updatePagination(data) {
        const { currentPage } = this.state;
        console.log('data.length : '+data.length);
        for (let i = 1; i < data.length / 10 + 1; i++) {

            console.log('currentPage : '+i);

            axios
            .get('https://test.ssirc.dev/Userprofile/paged/?page='+i)
            .then(pageResponse => {
                Promise
                    .all(pageResponse.data.results.map(user => {
                        data.forEach(element => {
                            if (element.User_id == user.User_id) {

                                var lastHrData = user.Hr[user.Hr.length - 1];
                                var lastSpo2Data = user.Spo2[user.Spo2.length - 1];
                                var lastAbnormalHrData = user.Stress[user.Stress.length - 1];
                                var lastStressData = user.Depress[user.Depress.length - 1];
                                var lastDepressData = user.Abnormal_hr[user.Abnormal_hr.length - 1];
                                var lastStepData = user.Step[user.Step.length - 1];
        
                                //to do : null 처리
                                lastHrData = lastHrData
                                    ? lastHrData.Hr
                                    : '미측정';
                                lastSpo2Data = lastSpo2Data
                                    ? lastSpo2Data.Spo2
                                    : '미측정';
                                lastAbnormalHrData = lastAbnormalHrData
                                    ? (lastAbnormalHrData.Abnormal_hr === true)
                                        ? 'Y'
                                        : 'N'
                                    : '미측정';
                                lastStressData = lastStressData
                                    ? lastStressData.Stress
                                    : '미측정';
                                lastDepressData = lastDepressData
                                    ? lastDepressData.Depress
                                    : '미측정';
                                lastStepData = lastStepData
                                    ? lastStepData.Step
                                    : '미측정';
        
                                if (lastDepressData === 3) 
                                    lastDepressData = "심각";
                                else if (lastDepressData === 2) 
                                    lastDepressData = "중증도";
                                else if (lastDepressData === 1) 
                                    lastDepressData = "경도";
                                else if (lastDepressData === 0) 
                                    lastDepressData = "정상";
                                
                                user.Gps_location = user.Gps_location
                                    ? user.Gps_location
                                    : 'no-idea';

                                element.hrData = lastHrData;
                                element.spo2Data = lastSpo2Data;
                                element.abnormalHrData = lastAbnormalHrData;
                                element.stressData = lastStressData;
                                element.depressData = lastDepressData;
                                element.stepData = lastStepData;
                            }
                        });
                        
                    }))
                    .then(() => {
                        this.setState({userDataStore: data});
                    })
                    .finally(()=>{
                        
                    });

            })
            .catch(userError => console.error('Error fetching userData', userError));
        }
    }

    componentDidMount() {
        this.checkProps();
        this.loadData();
        //setInterval(this.loadData, 5000)
    }

    DetailButton = ({label}) => {
        return (
            <Button
                style={{
                    paddingTop: '3px',
                    paddingBottom: '3px',
                    paddingLeft: '15px',
                    paddingRight: '15px',
                    margin: '0px',
                    width: '100%'
                }}>
                {label}
            </Button>
        );
    };

    handleSearch = () => {
        const { userDataStore, searchKeyword, selectedItem } = this.state;
        console.log('[handleSearch] userDataStore : '+userDataStore);
        console.log('[handleSearch] searchKeyword : '+searchKeyword);
        const originalUserData = [...this.state.userDataStore];

        if (originalUserData) {
            const filteredData = originalUserData.filter(
                item => (selectedItem.includes('이름') && item.Name.includes(searchKeyword)) || (selectedItem.includes('전화번호') && item.Hp_num.includes(searchKeyword)) || (selectedItem.includes('아이디') && item.Id.includes(searchKeyword))
            );

            // 필터링된 결과를 페이지네이션 처리하여 currentItems에 할당
            this.setState({
                currentPage: 1, // 검색 시 첫 번째 페이지로 초기화
                userData: filteredData
            });
        }
    };

    SearchButton = ({label}) => {
        console.log('search button!');
        return (
            <Button
                color='secondary'
                style={{
                    width: '100%'
                }}
                onClick={this.handleSearch}>
                {label}
            </Button>
        );
    };

    async loadData() {
        axios
            .get('https://api.ssirc.dev/api/User/')
            .then(userResponse => {
                const usersWithAdditionalData = [];

                Promise
                    .all(userResponse.data.map(user => {

                        const userWithAdditionalData = {
                            ...user,
                            hrData: '미측정',
                            spo2Data: '미측정',
                            abnormalHrData: '미측정',
                            stressData: '미측정',
                            depressData: '미측정',
                            stepData: '미측정'
                        };
                        usersWithAdditionalData.push(userWithAdditionalData);

                    }))
                    .then(() => {
                        this.setState({userData: usersWithAdditionalData});
                        console.log('userData with additional data: ', usersWithAdditionalData);
                        this.setState({userDataStore: usersWithAdditionalData});
                        console.log('userDataStore with additional data: ', usersWithAdditionalData);
                        
                        const returnedProp = this.props.match.params.returned;
                        const searchKeywordProp = this.props.match.params.searchKeyword;
                        const selectedItemProp = this.props.match.params.selectedItem;

                        if (returnedProp === 'yes' && searchKeywordProp !== 'null') {
                            const filteredData = usersWithAdditionalData.filter(
                                item => (selectedItemProp.includes('이름') && item.Name.includes(searchKeywordProp)) || (selectedItemProp.includes('전화번호') && item.Hp_num.includes(searchKeywordProp)) || (selectedItemProp.includes('아이디') && item.Id.includes(searchKeywordProp))
                            );

                            // 필터링된 결과를 페이지네이션 처리하여 currentItems에 할당
                            this.setState({
                                currentPage: 1, // 검색 시 첫 번째 페이지로 초기화
                                userData: filteredData
                            });
                        }
                        this.updatePagination(usersWithAdditionalData);
                    });
            })
            .catch(userError => console.error('Error fetching userData', userError));
    }

    render() {
        const {userData, currentPage, searchKeyword, itemsPerPage, selectedItem} = this.state;
        const totalPages = Math.ceil(userData.length / itemsPerPage);
        const indexOfLastItem = currentPage * itemsPerPage;
        const indexOfFirstItem = indexOfLastItem - itemsPerPage;
        const currentItems = userData.slice(indexOfFirstItem, indexOfLastItem);

        const paginate = pageNumber => {
            this.setState({currentPage: pageNumber});
        };

        let searchKeywordString = searchKeyword;
        if (searchKeywordString === '') {
            searchKeywordString = null;
        }

        return (
            <Fragment>
                <TransitionGroup>
                    <CSSTransition
                        component="div"
                        className="TabsAnimation"
                        appear={true}
                        timeout={0}
                        enter={false}
                        exit={false}>

                        <div className='whole-page-ui'>
                            <Row>
                                <Col>
                                    <p className='mb-0'>
                                        <span className='font-size-xxlarge mx-2'>나주시</span>
                                        <span className='font-size-xlarge'>건강 모니터링 시스템</span>
                                    </p>
                                </Col>
                                <Col className='font-size-small vertical-center horizontal-right'>Login &gt; 사용자 현황 관리</Col>
                            </Row>
                            <div className='space-10px'></div>
                            <div className='boader-black'></div>
                            <div className='space-20px'></div>
                            <Table className="mb-0" bordered={true}>
                                <tbody>
                                    <tr>
                                        <td className='font-size-large'>검색어</td>
                                        <td>
                                            <UncontrolledButtonDropdown>
                                                <DropdownToggle caret={true} color="secondary">
                                                    {selectedItem}
                                                </DropdownToggle>
                                                <DropdownMenu>
                                                    <DropdownItem onClick={() => this.handleDropdownItemSelect('아이디')}>아이디</DropdownItem>
                                                    <DropdownItem onClick={() => this.handleDropdownItemSelect('이름')}>이름</DropdownItem>
                                                    <DropdownItem onClick={() => this.handleDropdownItemSelect('전화번호')}>전화번호</DropdownItem>
                                                </DropdownMenu>
                                            </UncontrolledButtonDropdown>
                                        </td>
                                        <td>
                                            <FormGroup className='m-lg-0'>
                                                <Input
                                                    type="text"
                                                    id='keyword'
                                                    placeholder="검색어를 입력해 주세요"
                                                    value={searchKeyword}
                                                    onChange={(e) => this.setState({searchKeyword: e.target.value})}/>
                                            </FormGroup>
                                        </td>
                                        <td>
                                            <this.SearchButton label={'검색'}/>
                                        </td>
                                    </tr>
                                </tbody>
                            </Table>
                            <div className='space-20px'></div>
                            <Row>
                                <Col md='2' className='ms-auto'>
                                    {/* Button to toggle sorting order */}
                                    <Card className="font-icon-wrapper">
                                        <DownloadModal user={this.state.userData}></DownloadModal>
                                    </Card>

                                </Col>

                                <Col md='2' className='mb-0'>
                                    <Link to={`../../../../Infomation/Basic/no/null/null`}>
                                        <Button className="mb-2 me-2 ms-auto" color='alternate' style={{ width: '100%' }}>전체 사용자 보기</Button>
                                    </Link>
                                </Col>
                            </Row>
                            <Table hover={true} className="text-center">
                                <thead>
                                    <tr>
                                        <th>순번</th>
                                        <th>ID</th>
                                        <th>이름</th>
                                        <th>핸드폰</th>
                                        <th>위치</th>
                                        <th>스트레스</th>
                                        <th>우울증</th>
                                        <th>심박이벤트여부</th>
                                        <th>혈중산소포화도</th>
                                        <th>심박수</th>
                                        <th>활동량</th>
                                        <th>관리</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {
                                        currentItems.map((item, index) => (
                                            <tr key={item.User_id}>
                                                <td>{item.User_id}</td>
                                                <td>{item.Id}</td>
                                                <td>{item.Name}</td>
                                                <td>{item.Hp_num}</td>
                                                <td>{item.Gps_location}</td>
                                                <td>{item.stressData}</td>
                                                <td>{item.depressData}</td>
                                                <td>{item.abnormalHrData}</td>
                                                <td>{item.spo2Data}</td>
                                                <td>{item.hrData}</td>
                                                <td>{item.stepData}</td>
                                                <td>
                                                    <Link to={`../../../../../Dashboards/basic/${item.User_id}/members/${searchKeywordString}/${selectedItem}`}>
                                                        <this.DetailButton label={'챠트'}/>
                                                    </Link>
                                                </td>
                                            </tr>
                                        ))
                                    }
                                </tbody>
                            </Table>
                            <div className="pagination-container text-center">
                            <Pagination>
                                {
                                    Array
                                        .from({length: totalPages})
                                        .map((_, index) => (
                                            <PaginationItem key={index} active={index + 1 === currentPage}>
                                                <PaginationLink onClick={() => paginate(index + 1)}>
                                                    {index + 1}
                                                </PaginationLink>
                                            </PaginationItem>
                                        ))
                                }
                            </Pagination>
                            </div>
                        </div>
                    </CSSTransition>
                </TransitionGroup>
            </Fragment>
        )
    }
}
class DownloadModal extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            downloadModal: false,
            year: 0,
            month: 0,
            day: 0,
            dayCount: 0,
            monthCount: 0,
            downloading: true,
            download_data1: [],
            complete_download: false
        };

        this.toggle = this
            .toggle
            .bind(this);

        console.log('[DownloadModal] !!')

        const {user} = this.props;

        //this.searchData(user, 0, 0);
    }

    toggle() {
        console.log('download modal toggle called!');

        const {user} = this.props;
        const {dayCount, monthCount} = this.state;

        this.setState((prevState) => ({
            downloadModal: !prevState.downloadModal,
            download_data1: []
        }));

        this.searchData(user, dayCount, monthCount);
        console.log('[toggle]downloadModal: ', this.state.downloadModal);
    }

    // async setDate() {     var currentDate = new Date();     var year =
    // currentDate.getFullYear();     var month = ('0' + ( currentDate.getMonth() +
    // 1     )).slice(-2);  한 자리 수에 0 추가     var day = ('0' +
    // currentDate.getDate()).slice(-2);  한 자리 수에 0 추가 console.log('date(today) : '
    // + year + " " + month + " " + day); console.log(         "chartView1 : " +
    // this.state.chartView1 + ", chartView2 : " + this.state.chartView2     );
    // console.log(         "monthCount : " + this.state.monthCount + ", dayCount :
    // " + this.state.dayCount     );     var modifiedDate = new Date(currentDate);
    // modifiedDate.setDate( currentDate.getDate() + (this.state.dayCount) +
    // (this.state.monthCount * 30) );     year = modifiedDate.getFullYear(); month
    // = ('0' + ( modifiedDate.getMonth() + 1     )).slice(-2);  한 자리 수에 0 추가 day =
    // ('0' + modifiedDate.getDate()).slice(-2);  한 자리 수에 0 추가
    // console.log('date(selected) : ' + year + " " + month + " " + day);
    // this.setState({year: year, month: month, day: day}); }

    
    async searchData(data, dayCount, monthCount) {
        let csvData = [
            ["Time Stamp", "Name", "Data Type", "Value"]
        ];

        var currentDate = new Date();

        var year = currentDate.getFullYear();
        var month = ('0' + (
            currentDate.getMonth() + 1
        )).slice(-2); // 한 자리 수에 0 추가
        var day = ('0' + currentDate.getDate()).slice(-2); // 한 자리 수에 0 추가

        console.log('date(today) : ' + year + " " + month + " " + day);
        console.log(
            "chartView1 : " + this.state.chartView1 + ", chartView2 : " + this.state.chartView2
        );
        console.log("monthCount : " + monthCount + ", dayCount : " + dayCount);

        var modifiedDate = new Date(currentDate);
        modifiedDate.setDate(currentDate.getDate() + (dayCount) + (monthCount * 30));

        year = modifiedDate.getFullYear();
        month = ('0' + (
            modifiedDate.getMonth() + 1
        )).slice(-2); // 한 자리 수에 0 추가
        day = ('0' + modifiedDate.getDate()).slice(-2); // 한 자리 수에 0 추가

        console.log('date(selected) : ' + year + " " + month + " " + day);

        this.setState({year: year, month: month, day: day});

        console.log('searchData! ' + year + '년 ' + month + '월');

        const last_user_id = data[data.length - 1].User_id;

        console.log('download_data1 : ' + this.state.download_data1);

        data.forEach(element => {
            Promise
                .all([
                    axios.get(
                        'https://test.ssirc.dev/search/Hr/Ts/' + element.User_id + '/?search=' + year +
                        "-" + month
                    ),
                    axios.get(
                        'https://test.ssirc.dev/search/Spo2/Ts/' + element.User_id + '/?search=' +
                        year + "-" + month
                    )
                ])
                .then(([hrResponse, spo2Response]) => {
                    if (hrResponse.data.length > 0) {
                        (hrResponse.data.map(hrElement => {
                            const simpleData = [hrElement.Ts, element.Name, '심박수', hrElement.Hr];
                            csvData.push(simpleData);
                        }))
                    }

                    if (spo2Response.data.length > 0) {
                        (spo2Response.data.map(spo2Element => {
                            const simpleData = [spo2Element.Ts, element.Name, '산소포화도', spo2Element.Spo2];
                            csvData.push(simpleData);
                        }))
                    }

                    if (element.User_id === last_user_id) {
                        console.log('userResponse : ' + element.Name);
                        this.setState((prevState) => ({
                            downloading: !prevState.downloading,
                            complete_download: !prevState.complete_download,
                            download_data1: csvData
                        }));
                        console.log(
                            '[searchData]downloading : ' + this.state.downloading +
                            ' complete_download : ' + this.state.complete_download
                        )
                    }
                })
                .catch(
                    error => console.error(`Error fetching data for user ${element.User_id}`, error)
                );
        });
    };

    toggleChartView2(view) {

        const {user} = this.props;
        const {downloading} = this.state;

        var localDayCount = 0;
        var localMonthCount = 0;

        if (downloading === true) {} else {
            if (view === "-1month") {
                localMonthCount = this.state.monthCount - 1;
                localDayCount = this.state.dayCount;
            } else if (view === "+1month") {
                localMonthCount = this.state.monthCount + 1;
                localDayCount = this.state.dayCount;
            }
            this.setState((prevState) => ({
                downloading: !prevState.downloading
            }));

            this.setState({
                dayCount: localDayCount,
                monthCount: localMonthCount,
                download_data1: []
            }, () => {
                this.searchData(user, localDayCount, localMonthCount);
            });
        }
        console.log('[toggleChartView2]downloading : ' + downloading);

    }

    handleDownloadButtonClick = () => {
        const {user} = this.props;
        const {downloading, dayCount, monthCount} = this.state;

        

        if (downloading === true) {} else {
            this.setState((prevState) => ({
                downloading: !prevState.downloading
            }));

            this.setState({
                download_data1: []
            }, () => {
                this.searchData(user, dayCount, monthCount);
            });
        }
        console.log('[handleDownloadButtonClick]downloading : ' + downloading);
    };

    render() {

        let {
            year,
            month,
            download_data1,
            complete_download,
            dayCount,
            monthCount,
            file_name
        } = this.state;
        
        var currentDate = new Date();

        if (year === 0) {
            year = currentDate.getFullYear();
            month = ('0' + (
                currentDate.getMonth() + 1
            )).slice(-2); // 한 자리 수에 0 추가
        }

        file_name = currentDate.getFullYear() + '년'
                    + (currentDate.getMonth() + 1) + '월' 
                    + currentDate.getDate() + '일-' 
                    + currentDate.getHours() + '시' 
                    + currentDate.getMinutes() + '분' 
                    + currentDate.getSeconds() + '초' ;

        return (
            <span className="d-inline-block">
                <Button
                    color="alternate"
                    md={1}
                    style={{ width: '100%' }}
                    onClick={this.toggle}>
                    <p>{'다운로드'}</p>
                </Button>
                <Modal
                    isOpen={this.state.downloadModal}
                    toggle={this.toggle}
                    className={this.props.className}
                    backdrop={true}>
                    <ModalHeader toggle={this.toggle}>사용자 데이터 다운로드 {/* <ModalHeader toggle={this.toggle}>{year+'년 '+month+'월'} */}
                    </ModalHeader>
                    <ModalBody>
                        <Row className='mb-2'>
                            <Col className='horizontal-center font-size-large'>{`${year}년 ${month}월`}
                            </Col>
                        </Row>
                        <Row className='mb-1 horizontal-center'>
                            <Col md='2'>
                                <Button
                                    style={{
                                        width: '100%'
                                    }}
                                    color={'secondary'}
                                    onClick={() => this.toggleChartView2('-1month')}>
                                    <i className="lnr-chevron-left"></i>
                                </Button>
                            </Col>
                            <Col md='3' className='vertical-center'>
                                <Button
                                    style={{
                                        width: '100%'
                                    }}
                                    className='p-1p5'
                                    color={'secondary'}
                                    onClick={() => this.handleDownloadButtonClick()}>
                                    {
                                        download_data1.length > 0
                                            ? (
                                                <CSVLink data={download_data1} filename={`전체사용자-${file_name}`}>
                                                    <p
                                                        className='m-0'
                                                        style={{
                                                            color: 'white'
                                                        }}>{'다운로드'}</p>
                                                </CSVLink>
                                            )
                                            : (
                                                <p
                                                    className='m-0'
                                                    style={{
                                                        color: 'white'
                                                    }}>{'준비 중'}</p>
                                            )
                                    }
                                </Button>
                            </Col>
                            <Col md='2'>
                                <Button
                                    style={{
                                        width: '100%'
                                    }}
                                    color={'secondary'}
                                    onClick={() => this.toggleChartView2('+1month')}>
                                    <i className="lnr-chevron-right"></i>
                                </Button>
                            </Col>
                        </Row>
                    </ModalBody>
                </Modal>
            </span>
        );
    }
}
export default withRouter(BasicMembers);
